@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.error {
  color: red;
  font-size: 0.8rem;
  position: absolute;
}

.welcome {
  height: 100vh;
  background: url(/static/media/batman-wallpaper.27be95e8.jpg) left top;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 20rem;
}

.text-gradient {
  background: linear-gradient(to right, #30cfd0 0%, #9a4173 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 576px) {
  .welcome {
    padding: 0;
  }
}

.btn-lightblue:hover {
  color: #5cafff !important;
}

@media (max-width: 767px) {
  .radar {
    display: none;
  }
  .team-stats {
    margin-top: 10px;
  }

  .p-l {
    padding-left: 10rem;
  }
}

@media (max-width: 540px) {
  .p-l {
    padding: 0;
  }
}

.flex-container {
  display: flex;
  flex-direction: row;
  margin: auto;
}

.hero-info {
  width: 50% !important;
}

.flex-container .hero-image {
  width: 50%;
}

.flex-container .hero-image img {
  height: 35rem;
}

@media (min-width: 992px) {
  .flex-container .hero-info ul li {
    margin-top: 15px;
  }
}

@media (max-width: 992px) {
  .flex-container {
    flex-direction: column;
  }

  .flex-container .hero-image {
    width: 100%;
  }

  .hero-info {
    width: 100% !important;
  }

  .flex-container .hero-info {
    margin-top: 2rem;
  }

  .flex-container .hero-info ul li {
    margin-top: 5px;
  }
}

@media (max-width: 460px) {
  .flex-container .hero-image img {
    height: 75vh;
  }

  .hero-info {
    width: 100% !important;
  }
}

.h-500 {
  height: 500px;
}

.opacity {
  background-color: rgba(48, 44, 44, 0.9);
}

.h-4r {
  height: 4rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #1a2950 !important;
  color: white;
  font-family: 'Nunito Sans', sans-serif;
}

